body, * {
  margin: 0;
  width:100%;
  font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: lighter;
  box-sizing: border-box;
  color:rgb(14, 14, 14);
  user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  color: #ececec;
}

button{
  cursor: pointer;
}


.handlerDiv button {
  font-family: 'Segoe UI';
  font-weight: 400;
  display:flex;
  text-align: center;
  width:100%;
  margin: 4px;
  justify-content: center;
  align-content: center;
  padding: 0;
  border-radius: 5px;
  background-color: white;
  font-size: medium;
  border: 1px solid #ccc;
  box-shadow: 0px 0px 2px gray;
}




.handlerDiv button:hover{
  background-color:#ececec;
  border:   1px solid #ccc;
  box-shadow: 0px 0px 2px gray;
}

.handlerDiv{
  padding: 10px 20px;
  padding-top: 0px;
  width: 100%;
  display:flex;
  flex-direction: row !important;
  justify-content: space-between;
  align-items:center;
}

.app_title{
  width: 100%;
  background-color: #fff;
  padding:0px 10px;
  margin-bottom: 0px;
  text-align: center;
  display: flex;
  position: fixed;
  height: 50px;
  top:0;
  left:0;
}

.app_title h1{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 40px;
  font-weight: lighter;
  color: rgb(43, 95, 90);
  text-shadow: 3px 3px 3px rgba(213, 221, 231, 0.582);
}

.HourContainer{
  background-color: #ececec3b;
}

.HourContainer::-webkit-scrollbar{
  background: white;
  width: 8px;
  border-radius: 10px;
  border: 1px solid lightgray;
}

.HourContainer::-webkit-scrollbar-track{
  box-shadow: inset 0 0 10px white;
  border-radius: 10px;
}

.HourContainer::-webkit-scrollbar-thumb{
  background-color: transparent;
  border: 1px solid gray;
  /*background: linear-gradient(125deg, #f7e47d, #993111);*/
  border-radius: 10px;
}

.HourContainer::-webkit-scrollbar-thumb:hover{
  background-color: rgb(128, 119, 119);
  border: 1px solid gray;
  /*background: linear-gradient(125deg, #f7e47d, #993111);*/
  border-radius: 10px;
}

.text_input{
  width: 100%;
  display:block;
  padding:10px;
  border:2px solid #dfdfdf;
  outline: none;
  margin: 2px;
  border-radius:5px;
  overflow-x: scroll;
}

.read{
  width: 100%;
  display:block;
  padding:10px;
  background-color: #ececec95;
  border:2px solid #ececec95;
  outline: none;
  margin: 2px;
  border-radius:5px;
}

.text_input:focus{
  border:2px solid rgba(252, 154, 154, 0.507);
  color: rgba(255, 71, 71);
}

#icon-btn:hover {
  background-color: rgba(97, 97, 97, 0.26);
  border:1px solid rgb(158, 158, 158);
  border-radius: 50%;
  transition: 0.1s all ease-in-out;
}



.line{
  height: 2px;
  background-color: rgb(255, 255, 255);
  /* border-bottom: 0px solid #ccc; */
  box-shadow: 0px 3px 4px rgb(119, 118, 118);
}

.app_title button{
  width:60px;
}

.nav{
  position: fixed;
  height: 40px;
  top:60px;
  left:0;
  background-color: #fff;
  z-index: 1;
}

.HourContainer{
  height:68vh;
  position: fixed;
  top:170px;
  overflow-y: scroll;
  overflow-x: hidden;
}

#submit_rep, .add_hour_btn{
  width: 80px;
  margin: 8px;
  color: #0e96a8;
  margin-right: 0;
  align-self: flex-end;
  padding: 6px 5px;
  border:0;
  background-color: transparent;
  border-bottom: 1px solid transparent;
  font-size: medium;
}

#submit_rep:hover, .add_hour_btn:hover{
  background-color:  #088a670a;
  border-bottom: 1px solid #12a4b8;
  transition: 0.5s all ease-in-out;
}


#pushOptions{
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  padding: 0 25px;
}

#pushOptions p{
  width:max-content;
  padding: 6px 5px ;
  background-color: #12a4b811;
  color: #098d9e;
  border-left: 5px solid #12a4b8;
}

.footer{
  position: fixed;
  top: 92vh;
  left:0;
  height: 54px;
  /* border: 2px solid rgba(23, 177, 197, 0.267); */
  background-color: #109292;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  box-shadow: 0 0 4px seagreen;
}

.footer p{
  color: #dbf8f8;
  text-align: center;
}

.footer a{
  color: #e3e8e9;
  font-weight: 800;
  display: inline;
}

.text{
  text-decoration: none;
  margin:0;
  height:inherit;
  width: inherit;
  padding: 7px 10px;
}

.current{
  background-color: #12a4b811;;
}

.foot {
  border-top: 1px solid rgb(226, 46, 172);
  position: fixed;
  top: 92vh;
  left:0;
  height: 54px;
  background-color: transparent;
  color:rgb(179, 0, 104) !important;
  z-index: 5;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.foot a{
  color: #088a67 !important;
}

.foot * {
