}


@media screen and (max-width: 400px) {
  .HourContainer{
    height:63vh;
    position: fixed;
    top:210px;
    overflow-y: scroll;
  }
  .footer{
    width: 100%;
    position: fixed;
    top: 95vh;
    left:0;
    height: 32px;
    /* border: 2px solid rgba(23, 177, 197, 0.267); */
    background-color: #088a67;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    box-shadow: 0 0 7px seagreen;
    font-size: small;
  }
}


.HomeContainer{
  height: 80vh;
  width: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  padding:0;
  align-items: center;
  flex-direction: column;
  z-index: 1;
  position: fixed;
  top:0;
  left:0;
}

@keyframes loadAnimImg {
  0% { clip-path: circle(0%); }
  10% { clip-path: circle(10%); }
  25.000% { clip-path: circle(15%); }
  50.000% { clip-path: circle(25%); }
  75.000% { clip-path: circle(50%); }
  100.00% { clip-path: circle(100%); }
 }

 @keyframes loadAnimTxt {
  0% { clip-path: rgb(247, 240, 245) }
  10% { clip-path: rgb(248, 227, 242) }
  25% { color: rgb(253, 209, 240)}
  35% { color: rgb(241, 157, 216)  }
  55.000% { color: rgb(235, 101, 195) }
  70.000% { color: rgb(226, 46, 172) }
  85.000% { color: rgb(160, 31, 121) }
  100.00% { color: rgb(146, 22, 109) }
 }

.HomeContainer img {
  animation:  loadAnimImg 3s;
}

.HomeContainer h1, h3{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  padding-bottom: 5px;
  text-align: center;
  animation: loadAnimTxt 3s;
}

.HomeContainer h1 {
  color: rgb(187, 12, 149);
  font-size: 45px;
}

.HomeContainer h3 {
  font-weight: 100;
  font-style: italic;
  color: rgb(235, 45, 178);
}


.HomeContainer .load{
  padding:1px;
  width: 240px;
  background-color: rgb(247, 247, 247);
  color: black;
  margin-top: 50px;
  border: 1px solid rgb(241, 10, 172);
  margin-bottom: 0;
  height: 13px;
  font-size: 0;
  z-index:1;
  border-radius: 10px;
}

.ovc {
  top: 120px;
  height:75vh;
  background-color: #fafafa;
  overflow-y: hidden;
}

.ov{
  width: 98%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  background-color:#f8f5db;
  /* flex-direction: column;
  justify-content: space-evenly; */
}
.ov p {
  width: 100%;
  /* padding: 2px; */
  margin: 5px;
  display: flex;
  justify-content: space-around;
  align-items: stretch;
  border-top: 1px solid rgb(207, 73, 73);
  border-bottom: 1px solid rgb(207, 73, 73);
  border-left: 1px solid rgb(207, 73, 73);

}

.ovc h1{
  width: 100%;
  text-align: center;
  margin:0;
  background-color:#d8d66721;
  color: rgb(151, 153, 17);
  border-bottom: 1px solid #bbcf01;
  margin-bottom: 10px;
}

.ovc ul {
  list-style: none;
  padding: 10px
}

.ovc li {
  background-color: #e6f750;
  margin:5px;
  border-radius: 4px;
  height:max-content;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.ovc .hrs{
  width: 100px;
  background-color: #fcf805 !important;
  margin:0 2px;
  margin-right:8px;
  border-right: 1px solid #a1b306;
  padding: 10px;
}


/* .ovc li:hover {
  background-color: #bacf0123;
} */

.ov span {
  background-color: #ffffff;
  color: #a1b306;
  width: 100%;
  /* border-left: 1px solid greenyellow; */
  padding: 5px;
}

.box {
  color:rgb(243, 243, 243) !important;
  background-color:rgb(207, 73, 73) !important;
}

@keyframes loadAnim {
  0% {  width: 0px; }
  8.333% {  width: 8px;}
  16.667% { width: 16px;}
  25.000% { width: 25px;}
  33.333% { width: 33px;}
  41.667% { width: 41px;}
  50.000% { width: 50px;}
  58.333% { width: 58px;}
  66.667% { width: 66px;}
  75.000% { width: 75px;}
  83.333% { width: 83px;}
  91.667% { width: 91px;}
  100.00% { width: 100px;}
 }

 @keyframes loadAnimBtn {
  0% {  width: 0px; }
  8.333% {  width: 0px;}
  16.667% { width: 0px;}
  25.000% { width: 0px;}
  33.333% { width: 0px;}
  41.667% { width: 0px;}
  50.000% { width: 0px;}
  58.333% { width: 0px;}
  66.667% { width: 0px;}
  75.000% { width: 0px;}
  83.333% { width: 0px;}
  91.667% { width: 0px;}
  100.00% { width: 100px;}
 }


#status{
  background-color:rgb(241, 10, 172);
  z-index: 4;
  height:9px;
  border-radius: 10px;
  animation:  loadAnim 5s ease-in-out;
}

.HomeContainer .btnGo{
  display: block;
  width: 100px;
  margin-top: 20px;
  padding: 10px 0;
  text-align: center;
  font-size: 20px;
  border: rgb(241, 10, 172);
  background-color:rgb(241, 10, 172);
  color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgb(218, 104, 183);
  animation: loadAnimBtn 6s 1;
  text-decoration: none;
}


.rlogo{
  height: 230px;
  width: 240px; 
}

.rotate {
  width: 1000px;
  position: fixed;
  top:-150px;
  left: -80px;
  background-color: rgb(148, 20, 110);
  height: 250px;
  z-index: -1;
  transform: rotate(-20deg);
  color: white;
  display: flex;
  justify-content: baseline;
  align-items: center;
}

